import React from 'react'

const Login = () => {
    return (
        <div className='bg-[#F2F4F7] h-screen pt-12'>
            <div className='h-[450px] bg-white w-[350px] mx-auto pt-15 border border-gray-700 rounded-xl shadow-lg'>
                <form className='text-center flex flex-col gap-4 p-4'>
                    <div className='gap-2'>
                        <h1 className='text-2xl font-semibold'>Login User</h1>
                    </div>
                    <div clas>
                        <input type='email' placeholder='write email' className='py-2 w-full border-2 border-gray-300 ease-in duration-300 outline-none focus:border-gray-500 px-2' />
                    </div>
                    <div>
                        <input type='password' placeholder='write password' className='py-2 w-full border-2 border-gray-300 ease-in duration-300 outline-none focus:border-gray-500 px-2' />
                    </div>
                    <p className='text-xs relative top-4'>This form will store your personal information encrypted and safe. Click here to see <span className='text-blue-700 cursor-pointer ease-in duration-300'>privacy page.</span></p>
                    <button className='mt-4 bg-blue-400 hover:bg-blue-500 ease-in duration-300 cursor-pointer py-2 mx-auto px-8 rounded-sm'>Submit</button>
                </form>
            </div>
        </div>
    )
}

export default Login